export default {
	props: {
		item: {
			type: Object,
			default: {}
		}
	},
	data() {
		return {
			isGuanzhu:this.item.isGuanzhu,
			infoNum:this.item.infoNum,
		};
	},
	methods:{
		//关注操作
		guanZhuClick(){
			uni.showToast({
				title:'关注成功',
				icon:'success'
			})
			this.isGuanzhu = true
		},
		
		//顶踩操作
		operate(type){
			switch(type){
				case 'ding':
					if(this.infoNum.cNum == 1)
					 {
					     return ; 
					 }
					this.infoNum.dingNum ++;
					if(this.infoNum.cNum !== 0){
						this.infoNum.caiNum --;
					}
					this.infoNum.cNum = 1;
					break;
				case 'cai':
					if(this.infoNum.cNum == 2)  {
					     return ; 
					 }
					this.infoNum.caiNum ++;
					
					//若无进行 操作
					if(this.infoNum.cNum !== 0){
						this.infoNum.dingNum --;
					}
					this.infoNum.cNum = 2;
					break;
			}
		}
	}
}
